import * as React from "react";
import { WindowLocation } from "@reach/router";
import { AnimatePresence, motion } from "framer-motion";
import styles from "./topnavigation.module.scss";
import { useOnClickOutside } from "utils/hooks";
import { Language, Links } from "utils/constants";
import { NavigationItem } from "components/atoms/NavigationItem";
import { LanguageOptionItem } from "components/atoms/LanguageOptionItem";
import { LanguageIcon } from "components/atoms/icons/LanguagesIcon";
import { classNames } from "utils/strings";
import { Message } from "components/atoms/Message";
import { MusicIcon } from "components/atoms/icons/MusicIcon";
import { MoreIcon } from "components/atoms/icons/MoreIcon";
import { MembersIcon } from "components/atoms/icons/MembersIcon";
import { SearchIcon } from "components/atoms/icons/SearchIcon";

interface LanguageItemProps {
  type: "text" | "icon";
  className?: string;
}

const LanguageItem = ({ type, className }: LanguageItemProps) => {
  const [isLanguagesToggled, setLanguagesToggle] = React.useState(false);

  const languagesRef = React.useRef(null);
  useOnClickOutside(languagesRef, () => {
    setLanguagesToggle(false);
  });

  return (
    <div className={className} ref={languagesRef}>
      <NavigationItem
        type="button"
        handleClick={() => setLanguagesToggle(!isLanguagesToggled)}
        label="languages"
      >
        {type === "text" ? <Message text="languages" /> : <LanguageIcon />}
      </NavigationItem>
      <AnimatePresence>
        {isLanguagesToggled && (
          <motion.div
            initial={{ opacity: 0, y: -20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            transition={{
              duration: 0.3,
            }}
            className={styles.options}
          >
            <ul>
              <LanguageOptionItem
                to={"/en" + Links.Singles}
                handleClick={() => setLanguagesToggle(false)}
              >
                English
              </LanguageOptionItem>
              <LanguageOptionItem
                to={Links.Singles}
                handleClick={() => setLanguagesToggle(false)}
              >
                
              </LanguageOptionItem>
              <LanguageOptionItem
                to={"/zh" + Links.Singles}
                handleClick={() => setLanguagesToggle(false)}
              >
                
              </LanguageOptionItem>
            </ul>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
};

interface TopNavigationProps {
  locale: Language;
  location: WindowLocation;
}

export const TopNavigation = ({ location }: TopNavigationProps) => {
  const shouldShowLanguagesButton = React.useMemo(() => {
    const { pathname } = location;

    return pathname.includes("/cds/") || pathname.includes("/members-list/");
  }, [location]);

  return (
    <nav className={styles.container}>
      <div className={styles.navigationSmall}>
        <NavigationItem
          type="link"
          to={Links.About}
          className={styles.iconTextItem}
        >
          <MoreIcon className={styles.icon} />
          <span className={styles.iconText}>
            <Message text="more" />
          </span>
        </NavigationItem>
        <NavigationItem
          type="link"
          to={Links.Singles}
          className={styles.iconTextItem}
        >
          <MusicIcon className={styles.icon} />
          <span className={styles.iconText}>
            <Message text="music" />
          </span>
        </NavigationItem>
        <NavigationItem
          type="link"
          to={Links.FirstGeneration}
          className={styles.iconTextItem}
        >
          <MembersIcon className={styles.icon} />
          <span className={styles.iconText}>
            <Message text="members" />
          </span>
        </NavigationItem>
        <NavigationItem
          type="link"
          to={Links.Search}
          className={styles.iconTextItem}
        >
          <SearchIcon className={styles.icon} />
          <span className={styles.iconText}>
            <Message text="search" />
          </span>
        </NavigationItem>
      </div>
      <div className={styles.navigationLarge}>
        <div className={styles.leftItems}>
          <NavigationItem
            type="link"
            to={Links.Singles}
            className={styles.item}
          >
            <Message text="music" />
          </NavigationItem>
          <NavigationItem
            type="link"
            to={Links.FirstGeneration}
            className={styles.item}
          >
            <Message text="members" />
          </NavigationItem>
          <NavigationItem type="link" to={Links.Search} className={styles.item}>
            <Message text="search" />
          </NavigationItem>
        </div>
        <div className={styles.rightItems}>
          {shouldShowLanguagesButton ? (
            <LanguageItem
              type="text"
              className={classNames(styles.languagesText, styles.item)}
            />
          ) : null}
          <NavigationItem type="link" to={Links.About} className={styles.item}>
            <Message text="about" />
          </NavigationItem>
        </div>
      </div>
    </nav>
  );
};
